<template>
  <div class="stock-login">
    <div>
      <el-form>
        <el-form-item>
          <h2>Login</h2>
        </el-form-item>
        <el-form-item>
          <el-input
            v-model="userInfo.user_name"
            placeholder="Please Input Username">
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-input
            v-model="userInfo.password"
            placeholder="Please Input Password"
            show-password>
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            @click="login">
            Login
          </el-button>
          <el-button
            @click="register">
            Register
          </el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref } from 'vue'
import { useRouter } from 'vue-router'
import { ElForm, ElFormItem, ElInput, ElButton } from 'element-plus'
import { ROUTE_CONSOLE_DASHBOARD, ROUTE_REGISTER } from '@/router/config'
import { login_api } from '@/store/api'
import GlobalData from '@/store/index'
import axios from 'axios'
import { setCookie } from '@/utils/func'
export default defineComponent({
  components: {
    ElForm,
    ElFormItem,
    ElInput,
    ElButton,
  },
  setup(props, { attrs, slots, emit }) {
    const userInfo = ref({
      user_name: '',
      password: '',
    })
    const router = useRouter()
    const login = () => {
      axios
        .post(login_api, userInfo.value)
        .then(res => {
          const data = res.data
          const isValid = data.code === 1001
          if(!isValid) {
            return
          }
          GlobalData.userInfo.value = data.data
          setCookie('token', data.data.token, 86400000*30)
          axios.defaults.headers.Authorization = `Token ${data.data.token}`
          router.push(ROUTE_CONSOLE_DASHBOARD.use())
        })
    }
    const register = () => {
      router.push(ROUTE_REGISTER.use())
    }
    return {
      userInfo,
      login,
      register,
    }
  }
})
</script>

<style lang="scss" scoped>
  .stock-login {
    max-width: 250px;
    margin: 0 auto;
    text-align: center;
    background-color: #fff;
    padding: 10px 100px;
    box-shadow: var(--shadow);
  }
</style>
